#datetime-picker {
  width: auto;
  text-align: center;
  border-color: inherit;
  border-radius: 4px;
  margin-right: 15px;
  font-size: 18px;
  display: inline-block;
}

button {
  padding: 0, 8px;
  height: 25px;
  width: auto;
  text-align: center;
  background: var(--IRIS, #4d5ae5);
  color: #fff;
  border: none;
  border-radius: 4px;
  margin: 0;
  font-size: 16px;
  display: inline-block;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.timer {
  gap: 10px;
  display: flex;
}

.field {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.value {
  font-size: 25px;
}

.label {
  font-size: 20px;
}

